<template>
<div class="chooser-component">
    <ul class="chooser-list">
      <li
      	v-for="(item, index) in selections"
      	@click="choseSelection(index)"
      	:class="{active: checkInSelect(index)}"
      >{{item.label}}</li>
    </ul>
</div>
</template>

<script>
	export default {
		name: 'chooser',
		props: {
			selections: {
				type: Array,
				default () {
					return [{
						label: 'demo',
						value: 0
					}]
				}
			}
		},
		data () {
			return {
				select: [0],
			}
		},
		computed: {
			
		},
		methods: {
			choseSelection (index) {
				let idx = this.select.indexOf(index);
				if (this.select.includes(index)) {
					this.select.splice(idx, 1)
				} else {
					this.select.push(index)
				}
				this.$emit('on-change', this.select)
			},
			checkInSelect (index) {
				return this.select.includes(index);
			}
		}
	}
</script>

<style scoped>
	.chooser-component {
	  position: relative;
	  display: inline-block;
	}
	.chooser-list li{
	  display: inline-block;
	  border: 1px solid #e3e3e3;
	  height: 25px;
	  line-height: 25px;
	  padding: 0 8px;
	  margin-right: 5px;
	  border-radius: 3px;
	  text-align: center;
	  cursor: pointer;
	}
	.chooser-list li.active {
	  border-color: #4fc08d;
	  background: #4fc08d;
	  color: #fff;
	}
</style>


